.buttonGroup {
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 20px;
}

button {
  outline: none;
  background: #333333;
  border: none;
  display: inline-block;
  padding: 6px 18px;
  color: #eeeeee;
  margin-right: 10px;
  border-radius: 5px;
  font-size: 12px;
  cursor: pointer;
}

button.plain {
  background: none;
  color: #333333;
}


