.fillColor {
  padding: 4px 0px;
}

:local(.fieldPanel) {
  /* composes: fieldPanel from "../../control-panel.css"; */
  composes: fieldPanel from global;
}

:local(.fieldLabel) {
  /* composes: fieldLabel from "../../control-panel.css"; */
  composes: fieldLabel from global;
}

.selector {
  position: relative;
  border-radius: 3px;
  padding: 6px;
  margin-top: 2px;
  background-color: #cccccc;
}

.currentColor {
  width: 40px;
  height: 20px;
  margin-right: 6px;
}


