.addDataWrapper {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 801;
}

.underlay {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
}

.addData {
  background-color: #ffffff;
  margin-top: 40px;
  padding: 20px;
  border-radius: 6px;
}

.addDataDropArea {
  border: 1px dashed #999999;
  height: 300px;
  border-radius: 4px;
}

.uploadIcon {
  margin-top: 80px;
  text-align: center;
  color: #e0e0e0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.uploadText {
  margin-top: 20px;
  text-align: center;
  color: #cccccc;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.manualUpload {
  text-align: center;
  color: #cccccc;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.manualUploadLink {
  cursor: pointer;
}

.manualUploadLink:hover {
  text-decoration: underline;
}


