.layer {
  background: #eeeeee;
  padding: 10px 10px 10px 6px;
  border-radius: 3px;
  margin-bottom: 6px;
}

input.layerName  {
  background: none;
  padding: 0 2px;
  border: 1px solid #eeeeee;
  border-radius: 3px;
  outline: none;
  -o-transition: .3s;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}

input.layerName:hover {
  background: #cccccc;
  border: 1px solid #cccccc;
}

input.layerName:focus {
  background: #cccccc;
  border: 1px solid #cccccc;
}


