.radius {
  padding: 4px 0px;
}

.radius input {
  width: 100%;
  border: 1px solid #cccccc;
  border-radius: 3px;
  outline: none;
  color: #333333;
  background-color: #cccccc;
}

.radius input:hover {
  border: 1px solid #dddddd;
  background-color: #dddddd;
}

.radius input:focus {
  border: 1px solid #666666;
  background-color: #dddddd;
}

.slider {
  padding-right: 10px;
}

:local(.fieldLabel) {
  /* composes: fieldLabel from "../../control-panel.css"; */
  composes: fieldLabel from global;
}


