.colorSwatch {
  padding: 5px;
  margin: 0;
  cursor: pointer;
  border-radius: 4px;
}

.active {
  background-color: #dddddd;
}

.colorSwatch:hover {
  background-color: #dddddd;
}

.colorSwatch div:first-child {
  border-radius: 3px 0px 0px 3px;
}

.colorSwatch div:last-child {
  border-radius: 0px 3px 3px 0px;
}


