.colorSwatch {
  padding: 4px 0px;
}

:local(.fieldPanel) {
  /* composes: fieldPanel from "../../control-panel.css"; */
  composes: fieldPanel from global;
}

:local(.fieldLabel) {
  /* composes: fieldLabel from "../../control-panel.css"; */
  composes: fieldLabel from global;
}

.selector {
  position: relative;
  border-radius: 3px;
  padding: 6px;
  margin-top: 2px;
  background-color: #cccccc;
}

.currentColorSwatch {
  padding: 5px;
  margin: 0;
}

.currentColorSwatch div:first-child {
  border-radius: 3px 0px 0px 3px;
}

.currentColorSwatch div:last-child {
  border-radius: 0px 3px 3px 0px;
}


