.toggleOpen,
.toggleClosed {
  height: 30px;
  width: 30px;
  color: #333333;
  background-color: #ffffff;
  border-radius: 50%;
  position: fixed;
  top: 64px;
  z-index: 800;
  display: inline-block;
  text-align: center;
  -o-transition: .3s;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}

.toggleOpen {
  right: 350px;
}

.toggleClosed {
  right: 20px;
}

.toggleOpen::before,
.toggleClosed::before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.toggleOpen a,
.toggleClosed a {
  margin: auto;
  text-align: center;
  vertical-align: middle;
  padding: 0.5em;
  margin: -0.5em;
}


