.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 801;
  background: rgba(0, 0, 0, 0.5);
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-align-items: center;
  -ms-flex-align: center;
      align-items: center;
  overflow-x: hidden;
  overflow-y: auto;
}

.toolbox {
  max-height: 100%;
}

.tool {}

.header {}

.body {
  width: 400px;
  padding: 14px;
  margin: 20px;
  background: #ffffff;
  border-radius: 6px;
  color: #333333;
}

ul.tools {
  margin-left: 24px;
}

/* 
* ---------------------------------------------------
* Composed Classes
* ---------------------------------------------------
*/

:global {
  .toolTitle {
    font-family: 'Roboto', sans-serif;
    font-size: 1.1em;
  }

  .toolDescription {
    padding-bottom: 6px;
    margin-bottom: 10px;
    border-bottom: 1px solid #cccccc;
  }

  .control {
    margin: 4px 0px 14px 0px;
  }

  .controlLastChild {
    margin: 0;
    padding-bottom: 0;
  }

  .controlLabel {
    font-family: 'Roboto', sans-serif;
    font-size: 0.75em;
    text-transform: uppercase;
  }
}


