.controlPanelOpen,
.controlPanelClosed {
  width: 320px;
  position: fixed;
  top: 20px;
  right: 20px;
  bottom: 24px;
  z-index: 800;
  -o-transition: .3s;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
  border-radius: 5px;
  overflow: hidden;
}

.controlPanelOpen {
  right: 20px;
}

.controlPanelClosed {
  right: -340px;
  opacity: 0;
}

.controlPanelHeader {
  background-color: #ffffff;
  border-radius: 5px 5px 0px 0px;
  padding: 1px;
}

.controlPanel {
  background-color: #ffffff;
  border-radius: 0px 0px 5px 5px;
  position: relative;
  padding: 10px 10px 60px 10px;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.controlPanel::-webkit-scrollbar {
  height: 10px;
  width: 10px;
}

.controlPanel::-webkit-scrollbar-corner {
  background: #ffffff;
}

.controlPanel::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #666666;
  border: 3px solid #ffffff;
}

.controlPanel::-webkit-scrollbar-track {
  background: #ffffff;
}

/* 
* ---------------------------------------------------
* Composed Classes
* ---------------------------------------------------
*/
:global {
  .sectionTitle {
    font-family: 'Roboto', sans-serif;
    font-size: 1.1em;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }

  .fieldPanel {
    background-color: #cccccc;
    padding: 4px;
    border-radius: 3px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
  }

  .fieldLabel {
    font-family: 'Roboto', sans-serif;
    font-size: 0.75em;
    text-transform: uppercase;
  }
}


