:local(.title) {
  /* composes: toolTitle from './toolbox.css'; */
  composes: toolTitle from global;
}

:local(.description) {
  /* composes: toolDescription from './toolbox.css'; */
  composes: toolDescription from global;
}

:local(.control) {
  /* composes: control from './toolbox.css'; */
  composes: control from global;
}

:local(.controlLastChild) {
  /* composes: controlLastChild from './toolbox.css'; */
  composes: controlLastChild from global;
  padding-bottom: 20px;
}

:local(.controlLabel) {
  /* composes: controlLabel from './toolbox.css'; */
  composes: controlLabel from global;
}

:local(.warning) {
  /* composes: controlLabel from './toolbox.css'; */
  composes: controlLabel from global;
  color: #be6464;
}

.slider {
  padding-right: 15px;
}

.detailSlider {
  padding-left: 15px;
  padding-right: 15px;
}

.sliderManual input {
  width: 100%;
  border: 1px solid #cccccc;
  border-radius: 3px;
  outline: none;
  color: #333333;
  background-color: #cccccc;
}

.sliderManual input:hover {
  border: 1px solid #dddddd;
  background-color: #dddddd;
}

.sliderManual input:focus {
  border: 1px solid #666666;
  background-color: #dddddd;
}


