.wrapper {
  position: fixed;
  z-index: 1100;
  top: 40px;
  right: 40px;
  bottom: 40px;
  left: 40px;
  margin-bottom: 60px;
}

.underlay {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #222222;
  opacity: 0.5;
}

.header {
  position: relative;
  z-index: 1;
  background: #ffffff;
  border-radius: 5px 5px 0px 0px;
  padding: 10px 20px 0px 20px;
}

.content {
  position: relative;
  z-index: 1;
  padding: 0px 10px 10px 10px;
  background: #ffffff;
  height: 100%;
  border-radius: 0px 0px 5px 5px;
}



