.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 801;
  background: rgba(0, 0, 0, 0.5);
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-align-items: center;
  -ms-flex-align: center;
      align-items: center;
}

.confirmAlert {}

.body {
  width: 400px;
  padding: 30px;
  text-align: left;
  background: #fff;
  border-radius: 6px;
  color: #333333;
}

.title {
  font-family: 'Roboto', sans-serif;
  font-size: 1.2em;
}

.buttonGroup {
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin-top: 20px;
}

button {
  outline: none;
  background: #333333;
  border: none;
  display: inline-block;
  padding: 6px 18px;
  color: #eeeeee;
  margin-right: 10px;
  border-radius: 5px;
  font-size: 12px;
  cursor: pointer;
}

button.plain {
  background: none;
  color: #333333;
}

button.remove {
  background-color: #be6464;
  color: #ffffff;
}


